import React from "react";
import styles from "./Filter.module.css";
import FilterCheckBox from "./FilterCheckbox/FilterCheckbox";
import FilterTab from "./FilterTab/FilterTab";

const filter = (props) => {
  const filterClass = props.filterOpen ? styles.Filter_isopen : "";
  const specialismFilterClass = props.specialismFilterOpen
    ? styles.Filter__tab_isopen
    : "";
  const specialismBtnClass = props.specialismFilterOpen
    ? styles.Filter__toggle_isactive
    : "";
  const yearFilterClass = props.yearFilterOpen
    ? styles.Filter__tab_isopen
    : "";
  const yearBtnClass = props.yearFilterOpen
    ? styles.Filter__toggle_isactive
    : "";
  const schoolFilterClass = props.schoolFilterOpen
    ? styles.Filter__tab_isopen
    : "";
  const schoolBtnClass = props.schoolFilterOpen
    ? styles.Filter__toggle_isactive
    : "";
  const nameFilterClass = props.nameFilterOpen ? styles.Filter__tab_isopen : "";
  const nameBtnClass = props.nameFilterOpen
    ? styles.Filter__toggle_isactive
    : "";

  let clearbutton = null;
  let clearText = "Clear";
  if (props.app_type !== "competition") {
    clearText = clearText + " filters";
  }

  // Do we have filters set? If we do offer a clear button
  if (
    props.yearFiltersMap.size > 0 ||
    props.specialismsFiltersMap.size > 0 ||
    props.nameFilterValue !== "" ||
    props.schoolsFiltersMap.size > 0
  ) {
    clearbutton = (
      <button onClick={props.clearFilters} className={styles.Filter__clearbtn}>
        {clearText}
      </button>
    );
  }

  const yearFilters = props.filter_years.map((year) => {
    const key = "year_" + year;
    const id = "year_" + year;

    return (
      <FilterCheckBox
        handleUpdate={props.handleUpdateYearFilters}
        id={id}
        filtersMap={props.yearFiltersMap}
        name={year}
        key={key}
      />
    );
  });

  const speciliasmFilters = props.filter_sectors.map((filter) => {
    // The checkboxes inside
    const checkboxes = filter.specialisms.map((specialism) => {
      return (
        <FilterCheckBox
          handleUpdate={props.handleUpdateSpecialismFilters}
          id={specialism.id}
          filtersMap={props.specialismsFiltersMap}
          name={specialism.name}
          key={specialism.id}
        />
      );
    });

    // The title with the checkboxes rendered in it.
    return (
      <div key={filter.sector.id} className={styles.Filter__section}>
        <h3>{filter.sector.name}</h3>
        <div className={styles.Filter_section__checkboxes}>{checkboxes}</div>
      </div>
    );
  });

  const schoolFilterCheckboxes = props.school_filters.map((school) => {
    const key = "school_" + school.id;
    const id = "school_" + school.id;

    return (
      <FilterCheckBox
        handleUpdate={props.handleUpdateSchoolsFilters}
        id={id}
        filtersMap={props.schoolsFiltersMap}
        name={school.name}
        key={key}
      />
    );
  });

  const schoolFilters = (
    <>
      <div className={styles.Filter_section__checkboxes}>
        {schoolFilterCheckboxes}
      </div>
    </>
  );

  let buttonText = "Filter by:";
  // If it's a competition change the button text

  return (
    <>
      <div className={styles.Filterwrap}>
        <div className={styles.Filter_mob_header}>
          <button
            onClick={() => props.clickToggle()}
            className={styles.FilterToggle}
          >
            <i></i> Filter by
          </button>
          {clearbutton}
        </div>

        <div className={filterClass + " " + styles.Filter}>
          <div className={styles.FilterBtns_wrap}>
            <div className={styles.FilterBtns__title}>{buttonText}</div>
            <button
              type="button"
              onClick={props.clickToggleSpecialism}
              className={specialismBtnClass + " " + styles.Filter__toggle}
            >
              Specialism <i></i>
            </button>

            {props.app_type !== "school" && !props.courseIdSet ? (
              <>
                <button
                  type="button"
                  onClick={props.clickToggleSchool}
                  className={schoolBtnClass + " " + styles.Filter__toggle}
                >
                  School <i></i>
                </button>
              </>
            ) : (
              ""
            )}
            <button
              type="button"
              onClick={props.clickToggleName}
              className={nameBtnClass + " " + styles.Filter__toggle}
            >
              Name <i></i>
            </button>

            {props.allow_filter_year === true && props.filter_years.length > 1 ? (
              <button
                type="button"
                onClick={props.clickToggleYear}
                className={yearBtnClass + " " + styles.Filter__toggle}
              >
                Year <i></i>
              </button>
            ) : (
              ""
            )}

            {clearbutton}
          </div>

          <div className={styles.Filter__inner}>
            <div className={styles.Filter__inner_header}>
              <button onClick={() => props.clickToggle()}>Close</button>
              <div className={styles.FilterBtns__title}>{buttonText}</div>
            </div>
            <button
              type="button"
              onClick={props.clickToggleSpecialism}
              className={
                specialismBtnClass +
                " " +
                styles.Filter__toggle +
                " " +
                styles.MobileFilterBtn
              }
            >
              Specialism <i></i>
            </button>
            <FilterTab
              class={specialismFilterClass + " " + styles.Filter__tab}
              submitHandler={props.onSubmit}
            >
              {speciliasmFilters}
            </FilterTab>
            {props.app_type !== "school" ? (
              <>
                <button
                  type="button"
                  onClick={props.clickToggleSchool}
                  className={
                    schoolBtnClass +
                    " " +
                    styles.Filter__toggle +
                    " " +
                    styles.MobileFilterBtn
                  }
                >
                  School <i></i>
                </button>

                <FilterTab
                  class={schoolFilterClass + " " + styles.Filter__tab}
                  submitHandler={props.onSubmit}
                >
                  {schoolFilters}
                </FilterTab>
              </>
            ) : (
              ""
            )}
            <button
              type="button"
              onClick={props.clickToggleName}
              className={
                nameBtnClass +
                " " +
                styles.Filter__toggle +
                " " +
                styles.MobileFilterBtn
              }
            >
              Name <i></i>
            </button>
            <FilterTab
              class={nameFilterClass + " " + styles.Filter__tab}
              submitHandler={props.onSubmit}
            >
              <input
                type="text"
                onChange={(e) => props.handleNameFilterChange(e)}
                value={props.nameFilterValue}
                placeholder="Name"
                className={styles.Filter__name_input}
              />
            </FilterTab>

            {props.allow_filter_year === true && props.filter_years.length > 1 ? (
              <><button
                type="button"
                onClick={props.clickToggleYear}
                className={yearBtnClass +
                  " " +
                  styles.Filter__toggle +
                  " " +
                  styles.MobileFilterBtn}
              >
                Year <i></i>
              </button><FilterTab
                class={yearFilterClass + " " + styles.Filter__tab}
                submitHandler={props.onSubmit}
              >
                  {yearFilters}
                </FilterTab></>    
            ) : (
              ""
            )}
              
          </div>
        </div>
      </div>
    </>
  );
};

export default filter;
