import React from "react";
import { Field } from "formik";
import globalStyles from "../../../App.module.css";

const AtField = (props) => {
  return (
    <Field name={props.name}>
      {({ field, form: { touched, errors }, meta }) => {
        let fieldClass = globalStyles.atFormInput;
        if (meta.touched && meta.error) {
          fieldClass = [globalStyles.atFormInput, globalStyles.atFormInput_error].join(' ');
        } else if (meta.touched && !meta.error) {
          fieldClass = [globalStyles.atFormInput, globalStyles.atFormInput_valid].join(' ');
        }
        return (
          <div>
            {props.type !== "textarea" ? (
              <input
                type={props.type}
                placeholder={props.placeholder}
                className={fieldClass}
                {...field}
              />
            ) : (
              <textarea
                placeholder={props.placeholder}
                className={[fieldClass, globalStyles.atTextArea].join(' ')}
                {...field}
              />
            )}
            {meta.touched && meta.error && (
              <div className="input-error">{meta.error}</div>
            )}
          </div>
        );
      }}
    </Field>
  );
};
export default AtField;
