import React from "react";
import ReactGA from "react-ga";
import styles from "./Socials.module.css";
import EmailButtonModal from "../EmailButtonModal/EmailButtonModal";

const socials = (props) => {
  const sendEvent = () => {
    ReactGA.event({
      category: "Contacted User",
      action: "Contacted User",
      label: props.artistName,
    });
  };

  const clickHandler = (e) => {
    e.preventDefault();
    let link = e.currentTarget.getAttribute("href");
    sendEvent();
    window.open(link, "_blank");
  };

  let linkedin = null;
  if (props.project.linkedin_url) {
    linkedin = (
      <a
        href={props.project.linkedin_url}
        rel="noopener noreferrer"
        target="_blank"
        onClick={clickHandler}
        className={styles.socials__icon + " " + styles.socials__icon_linkedin}
      >
        Linkedin
      </a>
    );
  }

  let instagram = null;
  if (props.project.instagram_url) {
    instagram = (
      <a
        href={props.project.instagram_url}
        rel="noopener noreferrer"
        target="_blank"
        onClick={clickHandler}
        className={styles.socials__icon + " " + styles.socials__icon_instagram}
      >
        Instagram
      </a>
    );
  }

  let pinterest = null;
  if (props.project.pinterest_url) {
    pinterest = (
      <a
        href={props.project.pinterest_url}
        rel="noopener noreferrer"
        target="_blank"
        onClick={clickHandler}
        className={styles.socials__icon + " " + styles.socials__icon_pinterest}
      >
        Pinterest
      </a>
    );
  }

  let twitter = null;
  if (props.project.twitter_url) {
    twitter = (
      <a
        href={props.project.twitter_url}
        rel="noopener noreferrer"
        target="_blank"
        onClick={clickHandler}
        className={styles.socials__icon + " " + styles.socials__icon_twitter}
      >
        Twitter
      </a>
    );
  }

  let profile_icon = (
    <a
      href={props.project.profile_url}
      rel="noopener noreferrer"
      target="_blank"
      onClick={clickHandler}
      className={styles.socials__icon + " " + styles.socials__icon_profile}
    >
      Artsthread Profile
    </a>
  );

  let message = (
    <EmailButtonModal
      classList={styles.socials__icon + " " + styles.socials__icon_message}
      userId={props.project.userid}
      artistName={props.artistName}
    />
  );

  let icons = null;
  if (props.app_details.app_type === "competition") {
    icons = (
      <div className={styles.socials}>
        {profile_icon}
        {message}
      </div>
    );
  } else {
    icons = (
      <div className={styles.socials}>
        {linkedin}
        {instagram}
        {pinterest}
        {twitter}
        {profile_icon}
        {message}
      </div>
    );
  }

  return icons;
};

export default socials;
