import React, { useEffect, useState, useRef } from "react";
import axios from "axios";
import LoadingIcon from "../../components/LoadingIcon/LoadingIcon";
import Gallery from "../../components/Project/Gallery/Gallery";
import BackButton from "../../components/Project/BackButton/BackButton";
import ProjectMeta from "../../components/Projects/ProjectMeta/ProjectMeta";
import Socials from "../../components/Project/Socials/Socials";
import styles from "./Project.module.css";
import useQueries from "react-use-queries";
import dompurify from "dompurify";
import trackPage from "../../hoc/TrackPage/TrackPage";
import ProjectVideos from "../../components/Project/ProjectVideos/ProjectVideos";
import Votebox from "../../components/Project/VoteBox/VoteBox";

const Project = (props) => {
  const [project, setProject] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [loadingError, setLoadingError] = useState(false);
  const projectRef = useRef();

  const queries = {
    "(min-width: 768px)": "medium",
  };

  useEffect(() => {
    const slug = props.match.params.slug;
    let url =
      process.env.REACT_APP_API_URL +
      "/wp-json/artsthread/v1/app-project?slug=" +
      slug;

    // If a competition add a comp id to the string so we can test if they are actually part of the comp.
    if (props.app_details.app_type === "competition") {
      url = url += "&competition_id=" + props.app_details.competition_id;
    }

    // May have a page value in there for infinite scroll stuff, but let's leave that for now!
    axios
      .get(url)
      .then((response) => {
        const loadedProject = response.data;

        if (loadedProject.length > 0) {
          // Store the details in state.
          setProject(loadedProject[0]);
          setIsLoaded(true);
          const page = props.location.pathname;
          trackPage(page);
          projectRef.current.scrollIntoView();
        } else {
          // No project so return an error.
          setLoadingError(true);
        }
      })
      .catch((error) => {
        setLoadingError(true);
      });
  }, [
    props.location.pathname,
    props.app_details.app_type,
    props.match.params.slug,
    props.app_details.competition_id,
  ]);

  const Wrap = (props) => {
    const [[breakpoint = "small"], mediaQueryListener] = useQueries(queries);
    return (
      <div data-bp={breakpoint} className={styles.Project} ref={projectRef}>
        {mediaQueryListener}
        {props.children}
      </div>
    );
  };

  let content = null;
  if (loadingError) {
    content = <div>Error loading project</div>;
  } else if (!isLoaded) {
    content = <LoadingIcon />;
  } else if (isLoaded) {
    const sanitizer = dompurify.sanitize;

    let votebox = null;
    if (props.app_details.app_type === "competition" && props.app_details.allow_voting === true) {
      votebox = (
        <Votebox
          project={project}
          specialism_id={project.main_specialism_id}
          goBack={props.history.goBack}
          competition_id={props.app_details.competition_id}
        ></Votebox>
      );
    }

    content = (
      <Wrap>
        <BackButton goBack={props.history.goBack} />
        {project.images.length > 0 && <Gallery images={project.images} />}
        <div className={styles.Project__flex}>
          <div className={styles.Project__flex_col1}>
            <h3 className={styles.Project__title}>{project.title}</h3>
            <p className={styles.Project__title__sub}>
              {project.firstname} {project.surname}
            </p>
            <ProjectMeta
              specialisms={project.specialisms}
              userLocation={project.location}
              userSchool={project.school}
              userCourse={project.course}
              single="true"
              appType={props.app_details.app_type}
            />
            <Socials
              project={project}
              artistName={project.firstname + " " + project.surname}
              app_details={props.app_details}
            />
            {votebox}
          </div>

          <div className={styles.Project__flex_col2}>
            <div className={styles.Project__content}>
              <p
                dangerouslySetInnerHTML={{
                  __html: sanitizer(project.content),
                }}
              />
              <ProjectVideos videos={project.videos} />
            </div>
          </div>
        </div>
      </Wrap>
    );
  }

  return content;
};

export default Project;
