import React, { useState } from "react";
import SwiperCore, { Navigation, Pagination, Autoplay } from "swiper";
import CmPartialText from "./CmPartialText";
import { Swiper, SwiperSlide } from "swiper/react";
import styles from "./VideoCarousel.module.css";
import ModalCarousel from "../../ModalCarousel/ModalCarousel";
import LazyImage from "../../../components/LazyImage/LazyImage";

// install Swiper modules
SwiperCore.use([Navigation, Pagination, Autoplay]);

const CmVideoCarousel = ({ content, videos, orientation }) => {
  const [modalOpen, setModalOpen] = useState(false);
  const [firstVideo, setFirstVideo] = useState(true);
  const [activeSlide, setActiveSlide] = useState(0);

  const openModal = (i) => {
    setActiveSlide(i);
    setFirstVideo(false);
    setModalOpen(true);
  };

  const closeModal = () => {
    setActiveSlide(0);
    setModalOpen(false);
  };

  const handleActiveSlide = (i) => {
    setActiveSlide(i);
  };

  const slideChangeHandler = (e) => {
    if (firstVideo !== true) {
      handleActiveSlide(null);
    }

    setActiveSlide(e.activeIndex);
  };

  let slidesPerView = {
    sml: "1",
    med: "2",
    lrg: "2",
  };

  if (orientation === "portrait") {
    slidesPerView["sml"] = "2.2";
    slidesPerView["med"] = "4";
    slidesPerView["lrg"] = "5";
  }

  return (
    <div className="CmVideoCarousel">
      <CmPartialText
        title={content.title}
        text={content.text}
        titleColour={content.title_colour}
      />
      <Swiper
        navigation
        slidesPerView={slidesPerView.sml}
        className={styles.videocarouselswiper}
        autoplay={{
            delay: 5000,
            disableOnInteraction: false
        }}
        pagination={{
          type: "fraction",
          renderFraction: (currentClass, totalClass) => {
            return (
              '<span class="' +
              currentClass +
              '"></span>' +
              " of " +
              '<span class="' +
              totalClass +
              '"></span>'
            );
          },
        }}
        breakpoints={{
          768: {
            slidesPerView: slidesPerView.med,
            slidesPerGroup: slidesPerView.med,
          },
          1100: {
            slidesPerView: slidesPerView.lrg,
            slidesPerGroup: slidesPerView.lrg,
          },
        }}
      >
        {videos.map((video, i) => {
          return (
            <SwiperSlide key={i} className={styles.videocarouselswiper__slide}>
              <div
                className={styles.videocarouselswiper__slide__inner}
                onClick={() => openModal(i)}
              >
                <LazyImage
                  image={video.image.url}
                  title={video.title}
                  width="100%"
                />

                {video.title !== "" && (
                  <div className={styles.videocarouselswiper__slide__text}>
                    <h4>{video.title}</h4>
                    <div dangerouslySetInnerHTML={{ __html: video.text }} />
                  </div>
                )}
              </div>
            </SwiperSlide>
          );
        })}
      </Swiper>
      <ModalCarousel
        modalOpen={modalOpen}
        activeSlide={activeSlide}
        closeModal={closeModal}
        videos={videos}
        handleActiveSlide={handleActiveSlide}
        slideChangeHandler={slideChangeHandler}
        extraClass="land"
        orientation={orientation}
      />
    </div>
  );
};

export default CmVideoCarousel;
