import React from "react";
import styles from "./CmWrapper.module.css";

const CmWrapper = (props) => {
  let customCss = null;
  if (props.customCss !== "") {
    customCss = JSON.parse(props.customCss);
  }

  return (
    <>
      <div className={styles.cmwrapper} style={customCss}>
        {props.children}
      </div>
    </>
  );
};

export default CmWrapper;
