import React, { useState } from "react";
import ReactPlayer from "react-player";
import CmPartialText from "./CmPartialText";
import styles from "../../../App.module.css";
import SwiperCore, { Navigation } from "swiper";
import { Swiper, SwiperSlide } from "swiper/react";

// install Swiper modules
SwiperCore.use([Navigation]);

const CmFullWidthVideoCarousel = ({ content }) => {
  const [playingId, setPlayingId] = useState(null);

  return (
    <div>
      <CmPartialText
        title={content.title}
        text={content.text}
        titleColour={content.title_colour}
      />
      <Swiper
        navigation
        onSlideChange={(e) => setPlayingId(null)}
        className="fullwidth-video-swiper"
        // slidesPerView="1.2"
      >
        {content.videos.map((video, i) => {
          return (
            <SwiperSlide key={i}>
              <div className={styles.landscapevideo}>
                <ReactPlayer
                  url={video.vimeo_url}
                  controls={true}
                  playing={playingId === i}
                  onPlay={() => setPlayingId(i)}
                />
              </div>
            </SwiperSlide>
          );
        })}
      </Swiper>
    </div>
  );
};

export default CmFullWidthVideoCarousel;
