import React from 'react';
import {Link} from 'react-router-dom';
import LazyImage from '../../../components/LazyImage/LazyImage';
import ProjectMeta from '../ProjectMeta/ProjectMeta';
import styles from './ProjectPreview.module.css'

const projectPreview = (props) => {

  // Is this a winners preview? If it is it needs a band stating they are a winner.
  let winnerStrip = null
  if(props.winner) {
    winnerStrip = <div className={styles.projectpreview__winner + ' at-projectpreview__winner '}>Winner - {props.specialisms.split(',')[0]}</div>
  }

  return(
    <div className={styles.projectpreview + ' projectpreview'}>
      <div>
        <Link to={'/project/' + props.slug} className={styles.projectpreview__imagelink}>
          {winnerStrip}
          <LazyImage image={props.image} title={props.title} />
        </Link>
      </div>
      <div className={styles.projectpreview__text}>
        <h3>{props.title}</h3>
        <p className={styles.projectpreview__text__p}>{props.firstname} {props.surname}</p>
      </div>
      <div className={styles.projectpreview__meta}>
        <ProjectMeta specialisms={props.specialisms} userLocation={props.userLocation} />
      </div>
    </div>
  )
}

export default projectPreview;