import React from 'react';
import dompurify from 'dompurify';
import styles from './ProjectsHeader.module.css';

const projectsHeader = (props)=> {

  const sanitizer = dompurify.sanitize;

  let title = null;
  // Add a title if there is one set in props
  if (props.title) {
    title = <h2 dangerouslySetInnerHTML={{ __html: sanitizer(props.title) }} />
  }

  // Add text if there is some set in props
  let text = null;
  if (props.text) {
    text = <div dangerouslySetInnerHTML={{ __html: sanitizer(props.text) }} />
  }

  return(
    <div className={styles.projectheader}>
      {title}
      {text}
      { props.children }
    </div>
  );

}

export default projectsHeader;