import React from "react";
import CmPartialText from "./CmPartialText";
import LazyImage from "../../../components/LazyImage/LazyImage";
import styles from "./CmFullWidthImage.module.css";

const CmFullWidthImage = ({ content }) => {
  let image = null;

  if (content.image_link) {
    image = (
      <a href={content.image_link} target="_blank" rel="noopener noreferrer">
        <LazyImage image={content.image.url} title={content.image_alt_tag} />
      </a>
    );
  } else {
    image = (
      <LazyImage image={content.image.url} title={content.image_alt_tag} />
    );
  }

  return (
    <>
      <CmPartialText
        title={content.title}
        text={content.text}
        titleColour={content.titleColour}
      />
      <div className={styles.cmfullwithimg_wrap}>{image}</div>
    </>
  );
};

export default CmFullWidthImage;
