import React, { useState } from "react";
import ReactModal from "react-modal";
import styles from "../../../components/ModalCarousel/ModalCarousel.module.css";
import EmailForm from "../EmailForm/EmailForm";

const EmailButtonModal = ({ classList, userId, artistName }) => {
  const toggleModal = () => {
    setModalOpen(!modalOpen);
  };

  const [modalOpen, setModalOpen] = useState(false);

  return (
    <>
      <ReactModal
        isOpen={modalOpen}
        className={styles.modalcarousel__content}
        overlayClassName={styles.modalcarousel__overlay}
        onRequestClose={() => setModalOpen(!modalOpen)}
      >
        <EmailForm userId={userId} artistName={artistName}></EmailForm>
        <button onClick={toggleModal} className={styles.modalcarousel__close}>
          Close
        </button>
      </ReactModal>

      <button
        onClick={() => {
          toggleModal();
        }}
        className={classList}
      >
        Email
      </button>
    </>
  );
};

export default EmailButtonModal;
