import React from 'react';
import styles from './CourseLink.module.css';
import {Link} from 'react-router-dom';
import LazyImage from '../LazyImage/LazyImage'

const courseLink = (props)=> {

  let image = null;
  if(props.image) {
    image = <LazyImage image={props.image} alt={props.name} />
  }

  return (
    <div>
      <Link className={styles.CourseLink} to={'/projects/?course_id=' + props.id}>
        <div className={styles.CourseLink__media}>
        {image}
        </div>
        <div className={styles.CourseLink__text}>
          <h3>{props.name}</h3>
        </div>
      </Link>
    </div>
  )

}

export default courseLink;