import React from "react";
import { Route, Switch } from "react-router-dom";
import Home from "../../containers/Home/Home";
import Projects from "../../containers/Projects/Projects";
import Project from "../../containers/Project/Project";
import SchoolCourses from "../../containers/SchoolCourses/SchoolCourses";
import EventCourses from "../../containers/EventCourses/EventCourses";

const Routes = (props) => {
  return (
    <Switch>
      <Route
        path="/project/:slug"
        exact
        render={(matchProps) => (
          <Project
            {...matchProps}
            app_loaded={props.app_loaded}
            app_details={props.app_details}
          />
        )}
      />
      <Route
        path="/projects"
        exact
        render={(matchProps) => (
          <Projects
            {...matchProps}
            app_details={props.app_details}
            app_loaded={props.app_loaded}
            seed={props.random_seed}
            projects={props.projects}
            winners={props.winners}
            projectUpdater={props.projectUpdater}
            specialismsFiltersMap={props.specialismsFiltersMap}
            specialismsFiltersMapUpdate={props.specialismsFiltersMapUpdate}
            selectFiltersStateHandler={props.selectFiltersStateHandler}
            specialimsSelectedFilters={props.specialimsSelectedFilters}
            nameFilterValue={props.nameFilterValue}
            handleNameFilterChange={props.handleNameFilterChange}
            schoolsFiltersMap={props.schoolsFiltersMap}
            schoolsFiltersMapUpdate={props.schoolsFiltersMapUpdate}
            yearFiltersMap={props.yearFiltersMap}
            yearFiltersMapUpdate={props.yearFiltersMapUpdate}  
            clearFilters={props.clearFilters}
            updateProjectPageNumber={props.updateProjectPageNumber}
            projectsPage={props.projectsPage}
            totalProjectCount={props.totalProjectCount}
            projectTotalUpdater={props.projectTotalUpdater}
          />
        )}
      />
      <Route
        path="/event-courses"
        exact
        render={() => (
          <EventCourses
            app_details={props.app_details}
            app_loaded={props.app_loaded}
            updateProjectPageNumber={props.updateProjectPageNumber}
            projectUpdater={props.projectUpdater}
            clearFilters={props.clearFilters}
          />
        )}
      />
      <Route
        path="/school-courses"
        exact
        render={() => (
          <SchoolCourses
            app_details={props.app_details}
            app_loaded={props.app_loaded}
            updateProjectPageNumber={props.updateProjectPageNumber}
            projectUpdater={props.projectUpdater}
            clearFilters={props.clearFilters}
          />
        )}
      />
      <Route
        path="/"
        exact
        render={() => (
          <Home app_details={props.app_details} app_loaded={props.app_loaded} />
        )}
      />
    </Switch>
  );
};

export default Routes;
