import React, { useState, useEffect, useRef } from "react";
import Filter from "../../components/Projects/Filter/Filter";
import ProjectsHeader from "../../components/Projects/ProjectsHeader/ProjectsHeader";
import ProjectsWrapper from "../../components/Projects/ProjectsWrapper/ProjectsWrapper";
import ContentModules from "../../components/Projects/ContentModules/ContentModules";
import Pagination from "react-js-pagination";
import axios from "axios";
import styles from "./Projects.module.css";
import replaceUrlParam from "../../utils/utils";
import "../../components/Pagination/Pagination.css";

const Projects = (props) => {
  const [loadingError, setLoadingError] = useState(null);
  const [filterOpen, setFilterOpen] = useState(false);
  const [specialismFilterOpen, setSpecialismFilterOpen] = useState(false);
  const [yearFilterOpen, setYearFilterOpen] = useState(false);
  const [schoolFilterOpen, setSchoolFilterOpen] = useState(false);
  const [nameFilterOpen, setNameFilterOpen] = useState(false);
  const filterRef = useRef();

  useEffect(() => {
    const yearsAvailable = props.app_details.graduation_year !== null ? props.app_details.graduation_year.split(',') : [];
    const getListings = () => {
      // If we've not been sent any projects. Then do the Query.
      if (!props.projects) {
        // Ok so now we get the projects based on whether it's an event or a school.
        let url = process.env.REACT_APP_API_URL + "/wp-json/artsthread/v1/";

        // Event or school or competition?
        if (props.app_details.app_type === "event") {
          url += "app-projects?event_id=" + props.app_details.event_id;
        } else if (props.app_details.app_type === "school") {
          url += "app-projects?school_id=" + props.app_details.school_id;
        } else {
          // Competition
          url +=
            "app-projects?competition_id=" + props.app_details.competition_id;
        }
        // Add grad year
        url += "&graduation_year=" + props.app_details.graduation_year;

        const page = props.projectsPage - 1;

        if (page > 0) {
          url += "&page=" + page;
        }

        // Add the page number and the project per page
        url += "&ppp=" + props.app_details.projects_per_page;

        // Add the seed for random with order
        url += "&seed=" + props.seed;

        // Do we have a course_id from previous pages?
        // We have a course id.

        if (props.location.search) {
          const search = new URLSearchParams(props.location.search);
          const course_id = search.get("course_id");
          url += "&course_id=" + course_id;
        }

        // Do we have filters for specialisms?
        if (props.specialismsFiltersMap) {
          // Making a copy of the state queried filters so we can strip them of false results.
          const filters = props.specialismsFiltersMap;
          // Loop over the map
          // Find any keys with the value of false
          // Delete it by the index of the for loop you are in?
          filters.forEach((value, specialism) => {
            if (value === false) {
              filters.delete(specialism);
            }
          });
          // That's cleaned up the filters. Now let's add just the specialism to an array to send through the get request.
          const filtersArr = [];
          for (let specialism of filters.keys()) {
            filtersArr.push(specialism);
          }

          if (filtersArr.length > 0) {
            // Add it to the query URL
            url += "&specialisms=" + filtersArr;
          }
        }

        // Do we have a name to search for?
        if (props.nameFilterValue !== "") {
          url += "&name=" + props.nameFilterValue;
        }

        // Do we have filters for schools?
        if (props.schoolsFiltersMap) {
          // Making a copy of the state queried filters so we can strip them of false results.
          const filters = props.schoolsFiltersMap;
          // Loop over the map
          // Find any keys with the value of false
          // Delete it by the index of the for loop you are in?
          filters.forEach((value, school) => {
            if (value === false) {
              filters.delete(school);
            }
          });
          // That's cleaned up the filters. Now let's add just the specialism to an array to send through the get request.
          const filtersArr = [];
          for (let school of filters.keys()) {
            // Remove school_ from the start of the id.
            school = school.replace("school_", "");
            filtersArr.push(school);
          }

          if (filtersArr.length > 0) {
            // Add it to the query URL
            url += "&schools=" + filtersArr;
          }
        }

        // Do we have filters for years?
        if (props.yearFiltersMap && props.app_details.allow_filter_year === true && yearsAvailable.length > 1) {
          // Making a copy of the state queried filters so we can strip them of false results.
          const filters = props.yearFiltersMap;
          // Loop over the map
          // Find any keys with the value of false
          // Delete it by the index of the for loop you are in?
          filters.forEach((value, year) => {
            if (value === false) {
              filters.delete(year);
            }
          });
          // That's cleaned up the filters. Now let's add just the specialism to an array to send through the get request.
          const filtersArr = [];
          for (let year of filters.keys()) {
            // Remove school_ from the start of the id.
            year = year.replace("year_", "");
            filtersArr.push(year);
          }
  
          if (filtersArr.length > 0) {
            url = replaceUrlParam(url, 'graduation_year', filtersArr.toString());
          }
        }

        // Do we have override filters?
        if (props.app_details.override_filters) {
          // Add it to the query URL
          url += "&override_filters=" + props.app_details.override_filters;
        }


        // May have a page value in there for infinite scroll stuff, but let's leave that for now!
        axios
          .get(url)
          .then((response) => {
            const projects = response.data.projects;
            const winners = response.data.winners;
            // Update the projects held in state in Listings.js
            props.projectUpdater(projects, winners);
            props.projectTotalUpdater(response.data.count);

            // Now should we scroll to the top of the projects?
            // Do we have filters set?
            // ScrollToFilterTop
            if (
              props.yearFiltersMap.size > 0 ||
              props.schoolsFiltersMap.size > 0 ||
              props.specialismsFiltersMap.size > 0 ||
              props.nameFilterValue !== ""
            ) {
              filterRef.current.scrollIntoView();
            }
          })
          .catch(() => {
            setLoadingError(true);
          });

        // Close the filter window:
        closeFilterHandler();
      }
    };

    getListings();
  }, [props, props.projects]);

  const toggleFilterHandler = () => {
    setFilterOpen(!filterOpen);
  };

  const toggleYearFilterHandler = () => {
    setYearFilterOpen(!yearFilterOpen);
    setSpecialismFilterOpen(false);
    setSchoolFilterOpen(false);
    setNameFilterOpen(false);
  };
  const toggleSpecialismFilterHandler = () => {
    setSpecialismFilterOpen(!specialismFilterOpen);
    setYearFilterOpen(false);
    setSchoolFilterOpen(false);
    setNameFilterOpen(false);
  };

  const toggleSchoolFilterHandler = () => {
    setSchoolFilterOpen(!schoolFilterOpen);
    setYearFilterOpen(false);
    setSpecialismFilterOpen(false);
    setNameFilterOpen(false);
  };

  const toggleNameFilterHandler = () => {
    setNameFilterOpen(!nameFilterOpen);
    setYearFilterOpen(false);
    setSchoolFilterOpen(false);
    setSpecialismFilterOpen(false);
  };

  const clearFiltersHandler = () => {
    props.clearFilters();
    // Set the pagination back to page 1
    props.updateProjectPageNumber(1);
    props.projectUpdater(false);
  };

  const closeFilterHandler = () => {
    setFilterOpen(false);
    setNameFilterOpen(false);
    setSchoolFilterOpen(false);
    setSpecialismFilterOpen(false);
    setYearFilterOpen(false);
  };

  const handleFilterSubmit = () => {
    // Clear the projects from props
    props.projectUpdater(false);
    // Set the pagination back to page 1
    props.updateProjectPageNumber(1);
  };

  const handlePageChange = (pageNumber) => {
    // Clear the the projects from props.
    props.projectUpdater(false);
    // Here I should push to the listings the pageNumber.
    props.updateProjectPageNumber(pageNumber);
  };

  const { projects } = props;

  // Back button
  let backbutton = null;
  let courseIdSet = false;
  if (props.location.search) {
    backbutton = (
      <button onClick={props.history.goBack} className={styles.Projects__back}>
        <i></i> Back to courses
      </button>
    );

    courseIdSet = true;
  }

  // Create the projects header with the text we've just created.
  let header = null;
  let winners = null;
  let content = null;
  if (props.app_loaded) {
    // Do we have any winners from a comp? If there are no filters selected then show them.
    if (
      props.winners &&
      props.winners.length !== 0 &&
      props.specialismsFiltersMap.size === 0
    ) {
      // Winners are pulled in here
      winners = (
        <ProjectsWrapper
          projects={props.winners}
          loadingError={loadingError}
          winner="true"
        />
      );
    }
    
    if (props.app_details.app_type !== "school") {
      content = <ContentModules contentModules={props.app_details.content_modules} />
    }

    header = (
      <ProjectsHeader
        title={props.app_details.listings_title}
        text={props.app_details.listings_text}
      >
        {content}
        {winners}
        {backbutton}

        {/* Here we need to bind the functions to update and submit filters as props to Filter */}
        <div ref={filterRef}>
          {!props.app_details.hide_projects && (
          <Filter
            filter_sectors={props.app_details.filter_sectors}
            school_filters={props.app_details.school_filters}
            filter_years={props.app_details.graduation_year !== null ? props.app_details.graduation_year.split(',') : []}
            allow_filter_year={props.app_details.allow_filter_year}
            onSubmit={handleFilterSubmit}
            specialismsFiltersMap={props.specialismsFiltersMap}
            handleUpdateSpecialismFilters={props.specialismsFiltersMapUpdate}
            handleNameFilterChange={props.handleNameFilterChange}
            schoolsFiltersMap={props.schoolsFiltersMap}
            handleUpdateSchoolsFilters={props.schoolsFiltersMapUpdate}
            yearFiltersMap={props.yearFiltersMap}
            handleUpdateYearFilters={props.yearFiltersMapUpdate}
            clickToggle={toggleFilterHandler}
            clickToggleYear={toggleYearFilterHandler}
            clickToggleSpecialism={toggleSpecialismFilterHandler}
            clickToggleSchool={toggleSchoolFilterHandler}
            clickToggleName={toggleNameFilterHandler}
            filterOpen={filterOpen}
            schoolFilterOpen={schoolFilterOpen}
            specialismFilterOpen={specialismFilterOpen}
            nameFilterOpen={nameFilterOpen}
            yearFilterOpen={yearFilterOpen}
            clickCloseFilter={closeFilterHandler}
            nameFilterValue={props.nameFilterValue}
            clearFilters={clearFiltersHandler}
            app_type={props.app_details.app_type}
            courseIdSet={courseIdSet}
          />
          )}
        </div>

      </ProjectsHeader>
    );
  }

  let pagination = null;
  if (
    props.app_details.projects_per_page < props.totalProjectCount &&
    props.projects
  ) {
    pagination = (
      <Pagination
        activePage={props.projectsPage}
        itemsCountPerPage={+props.app_details.projects_per_page}
        totalItemsCount={props.totalProjectCount}
        pageRangeDisplayed={5}
        onChange={handlePageChange}
        innerClass="Pagination"
        activeLinkClass="active"
        hideDisabled={true}
      />
    );
  }

  return (
    <div className="Projects">
      {header}
      {!props.app_details.hide_projects && (
      <>
        <ProjectsWrapper projects={projects} loadingError={loadingError} />
        {pagination}
      </>
      )}
    </div>


  );
};

export default Projects;
