import React from "react";
import styles from "./CmTwoColumnText.module.css";

const CmTwoColumnText = ({ content }) => {
  return (
    <>
      {content.title !== "" ? (
        <h2 style={content.titleStyle}>{content.title}</h2>
      ) : (
        ""
      )}

      <div className={styles.CmTwoColumnText}>
        {content.text_1 !== "" ? (
          <div dangerouslySetInnerHTML={{ __html: content.text_1 }} />
        ) : (
          ""
        )}

        {content.text_2 !== "" ? (
          <div dangerouslySetInnerHTML={{ __html: content.text_2 }} />
        ) : (
          ""
        )}
      </div>
    </>
  );
};

export default CmTwoColumnText;
