import React, { useState } from "react";
import { Formik, Form } from "formik";
import * as Yup from "yup";
import AtField from "./AtField";
import axios from "axios";
import qs from "qs";
import styles from "./EmailForm.module.css";
import globalStyles from "../../../App.module.css";

const SignupSchema = Yup.object().shape({
  email: Yup.string().email("Invalid email").required("Required"),
  message: Yup.string().required("Required"),
});

const EmailForm = ({ userId, artistName }) => {
  const [messageSent, setMessageSent] = useState(false);

  const handleSubmit = (email, message, { setSubmitting, resetForm }) => {
    // Send the email.
    const url =
      process.env.REACT_APP_API_URL + "/wp-json/artsthread/v1/app-send-email";
    const data = qs.stringify({
      email: email,
      message: message,
      id: userId,
    });
    // Do the axios POST here to update the vote.
    axios
      .post(url, data)
      .then((response) => {
        const result = response.data;
        if (result === "success") {
          setMessageSent(true);
          resetForm();
        }
        setSubmitting(false);
      })
      .catch((error) => {
        alert("There was an error please try again");
        setSubmitting(false);
      });
  };

  return (
    <>
      <div className={styles.emailForm}>
        <h3>Message {artistName}</h3>
        <Formik
          initialValues={{ email: "", message: "" }}
          validationSchema={SignupSchema}
          onSubmit={(values, { setSubmitting, resetForm }) => {
            setMessageSent(false);
            handleSubmit(values.email, values.message, {
              setSubmitting,
              resetForm,
            });
          }}
        >
          {({ isSubmitting }) => (
            <Form className="at-form">
              <AtField
                name="email"
                type="email"
                placeholder="Your Email Address"
              />
              <AtField
                name="message"
                type="textarea"
                placeholder="Your message"
              />
              <button
                type="submit"
                disabled={isSubmitting}
                className={globalStyles.atAppBtnWhite}
              >
                {isSubmitting ? "Sending" : "Send"}
              </button>
              {messageSent ? <p>Your message has been sent</p> : ""}
            </Form>
          )}
        </Formik>
      </div>
    </>
  );
};

export default EmailForm;
