import React from "react";
import ReactPlayer from "react-player";
import styles from "./ProjectVideos.module.css";

const projectVideos = (props) => {
  if (!props.videos) {
    return false;
  }

  const videos = props.videos.map((videos, index) => {
    return (
      <div className={styles.ProjectVideo} key={index}>
        <ReactPlayer url={videos.vidurl} controls />
      </div>
    );
  });

  return videos;
};

export default projectVideos;
