import React, { useState, Fragment } from "react";
// My slick theme
import "./Gallery-global.css";

import GallerySliderWrapper from "./GallerySliderWrapper/GallerySliderWrapper";
import Carousel, { Modal, ModalGateway } from "react-images";

const Gallery = (props) => {
  const [selectedIndex, setSelectedIndex] = useState(0);
  const [lightboxIsOpen, setlightboxIsOpen] = useState(false);

  const toggleLightbox = (selectedIndex) => {
    setlightboxIsOpen(!lightboxIsOpen);
    setSelectedIndex(selectedIndex);
  };

  /* First loop over the props.images and add them to a new array
  that is written with source for the react-images modal */
  const modalImages = props.images.map((image) => {
    return { source: image.image_medium };
  });

  if (props.images.size < 1) {
    return false;
  }

  return (
    <Fragment>
      <GallerySliderWrapper
        images={props.images}
        toggleLightbox={toggleLightbox}
      />
      <ModalGateway>
        {lightboxIsOpen ? (
          <Modal
            onClose={toggleLightbox}
            allowFullscreen={false}
            styles={{
              blanket: (base) => ({
                ...base,
                background: "rgba(0, 0, 0, 0.9)",
              }),
            }}
          >
            <Carousel
              currentIndex={selectedIndex}
              views={modalImages}
              styles={{
                container: (base) => ({
                  ...base,
                  height: "100vh",
                }),
                view: (base) => ({
                  ...base,
                  alignItems: "center",
                  display: "flex ",
                  height: "calc(100vh - 54px)",
                  justifyContent: "center",

                  "& > img": {
                    maxHeight: "calc(100vh - 94px)",
                  },
                }),
                navigationPrev: (base) => ({
                  ...base,
                  backgroundColor: "#000",
                  borderRadius: 0,
                  ":hover": {
                    outline: "none",
                    backgroundColor: "#444",
                  },
                  ":active": {
                    outline: "none",
                    backgroundColor: "#444",
                  },
                }),
                navigationNext: (base) => ({
                  ...base,
                  backgroundColor: "#000",
                  borderRadius: 0,
                  ":hover": {
                    outline: "none",
                    backgroundColor: "#444",
                  },
                  ":active": {
                    outline: "none",
                    backgroundColor: "#444",
                  },
                }),
              }}
            />
          </Modal>
        ) : null}
      </ModalGateway>
    </Fragment>
  );
};

export default Gallery;
