import React from "react";
import { Redirect } from "react-router-dom";

const Home = (props) => {
  // Here we are just using this route to render a redirect for now.
  // Eventually this could become a 'page' in it's own right

  let content = null;

  // If the app is loaded then there will be info for the redirects.
  if (props.app_loaded) {
    // Redirect based on what's in the config.
    // Check we don't have a project slug in the url
    if (
      !props.app_details.show_course_index ||
      props.app_details.app_type === "competition"
    ) {
      // Do we just straight to project listings?
      content = <Redirect to="/projects"></Redirect>;
    } else {
      // Or do we need to show and index of the courses?
      if (props.app_details.app_type === "event") {
        // Do the courses relate to an event?
        content = <Redirect to="/event-courses"></Redirect>;
      } else {
        // Or to a shool?
        content = <Redirect to="/school-courses"></Redirect>;
      }
    }
  }

  return content;
};

export default Home;
