import React from "react";
import styles from "./ProjectMeta.module.css";

const projectMeta = (props) => {
  let metaClass = null;
  if (props.single) {
    metaClass = styles.projectmeta_single;
  }

  return (
    <div className={styles.projectmeta + " " + metaClass}>
      <p>
        <strong>Specialism:</strong> {props.specialisms}
      </p>
      <p>
        <strong>Location:</strong> {props.userLocation}
      </p>
      {props.userSchool && props.appType !== "school" && (
        <p>
          <strong>University/School:</strong> {props.userSchool}
        </p>
      )}
      {props.userCourse && (
        <p>
          <strong>Course/Program:</strong> {props.userCourse}
        </p>
      )}
    </div>
  );
};

export default projectMeta;
