import React from "react";
import styles from "./EmailForm.module.css";

const EmailForm = (props) => {
  let sendingMessage = null;
  if (props.isDisabled) {
    sendingMessage = <p>Subscribing...</p>;
  }

  return (
    <form className={styles.email_form} onSubmit={props.handleEmailForm}>
      <p>
        To be kept in the loop about the competition winners please enter your
        details below:
      </p>
      <input
        type="text"
        placeholder="Full name"
        required
        className={styles.email_form__email}
        onChange={props.inputNameChange}
      />
      <div className={styles.email_form__fields}>
        <input
          type="email"
          placeholder="Email address"
          required
          className={styles.email_form__email}
          onChange={props.inputChange}
        />
        <button
          type="submit"
          className={styles.email_form__submit}
          disabled={props.isDisabled}
        >
          GO
        </button>
      </div>
      {sendingMessage}
      <input
        type="checkbox"
        name="artsthread-updates"
        id="artsthread-updates"
        onChange={props.checkboxChange}
      />
      <label
        htmlFor="artsthread-updates"
        className={styles.email_form__checkboxlabel}
      >
        Also keep me informed about ARTSTHREAD NEWS.
      </label>
    </form>
  );
};

export default EmailForm;
