import React, { useState, useEffect, Fragment } from "react";
import ProjectsHeader from "../../components/Projects/ProjectsHeader/ProjectsHeader";
import LoadingIcon from "../../components/LoadingIcon/LoadingIcon";
import CourseLinkWrapper from "../../components/CourseLinkWrapper/CourseLinkWrapper";
import axios from "axios";

const CoursesIndex = ({
  apiEndpoint,
  queryId,
  projectUpdater,
  updateProjectPageNumber,
  clearFilters,
  app_details,
  app_loaded,
}) => {
  const [courses, setCourses] = useState();
  const [isLoaded, setIsLoaded] = useState(false);
  const [loadingError, setLoadingError] = useState(false);

  useEffect(() => {
    // Get all the courses that relate to this course.
    const url =
      process.env.REACT_APP_API_URL +
      "/wp-json/artsthread/v1/" +
      apiEndpoint +
      "?id=" +
      queryId +
      "&app_post_id=" +
      app_details.app_post_id +
      "&graduation_year=" +
      app_details.graduation_year;

    // May have a page value in there for infinite scroll stuff, but let's leave that for now!
    axios
      .get(url)
      .then((response) => {
        const courses = response.data;
        // Store the details in state.
        setCourses(courses);
        setIsLoaded(true);
      })
      .catch((error) => {
        setLoadingError(true);
      });
  }, [apiEndpoint, app_details.app_post_id, app_details.graduation_year, queryId]);

  useEffect(() => {
    // Reset the projects and pagination and any filters
    projectUpdater(false);
    updateProjectPageNumber(1);
    clearFilters();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // Create the projects header with the text we've just created.
  let header = null;

  if (app_loaded) {
    header = (
      <ProjectsHeader
        title={app_details.index_title}
        text={app_details.index_text}
      />
    );
  }

  let content = <LoadingIcon />;

  if (isLoaded && !loadingError) {
    content = <CourseLinkWrapper courses={courses} />;
  }

  return (
    <Fragment>
      {header}
      {content}
    </Fragment>
  );
};

export default CoursesIndex;
