import React, { useState, useEffect } from "react";
import axios from "axios";
import Routes from "../../components/Routes/Routes";
import LoadingIcon from "../../components/LoadingIcon/LoadingIcon";
import LoadingError from "../../components/LoadingError/LoadingError";
import ReactGA from "react-ga";

const Listings = () => {
  const [appDetails, setAppDetails] = useState([]);
  const [appLoaded, setAppLoaded] = useState(false);
  const [appLoadingError, setAppLoadingError] = useState(false);
  const [randomSeed] = useState(+new Date());
  const [projects, setProjects] = useState(false);
  const [winners, setWinners] = useState(false);
  const [projectsPage, setProjectsPage] = useState(1);
  const [totalProjectCount, setTotalProjectCount] = useState();
  // Filter states
  const [specialismsFiltersMap, setSpecialismsFiltersMap] = useState(new Map());
  const [nameFilterValue, setNameFilterValue] = useState("");
  const [schoolsFiltersMap, setSchoolsFiltersMap] = useState(new Map());
  const [yearFiltersMap, setYearFiltersMap] = useState(new Map());

  // Stores the projects loaded in state here.
  // This is so it can be held in 'memory' when going back and forth between /projects and /project view
  const handleProjectsState = (projects, winners) => {
    setProjects(projects);
    setWinners(winners);
  };

  const totalProjectsHandler = (data) => {
    setTotalProjectCount(data);
  };

  const handleProjectPaginationChange = (page) => {
    setProjectsPage(page);
  };

  // Stores the selected filters here.
  // This is so it can be held in 'memory' when going back and forth between /projects and /project view
  const specialismsFiltersMapUpdate = (e) => {
    // Function to update the filters state.
    // Will be triggered by the checking of a checkbox component way down
    const item = e.target.name;
    const isChecked = e.target.checked;

    setSpecialismsFiltersMap((prevState) => {
      return new Map(prevState).set(item, isChecked);
    });
  };

  // School filter changes
  const schoolsFiltersMapUpdate = (e) => {
    const item = e.target.name;
    const isChecked = e.target.checked;

    setSchoolsFiltersMap((prevState) => {
      return new Map(prevState).set(item, isChecked);
    });
  };

  // Stores the selected filters here.
  // This is so it can be held in 'memory' when going back and forth between /projects and /project view
  const yearFiltersMapUpdate = (e) => {
    // Function to update the filters state.
    // Will be triggered by the checking of a checkbox component way down
    const item = e.target.name;
    const isChecked = e.target.checked;

    setYearFiltersMap((prevState) => {
      return new Map(prevState).set(item, isChecked);
    });
  };

  const handleNameFilterChange = (e) => {
    const val = e.target.value;
    setNameFilterValue(val);
  };

  const filtersMapClear = () => {
    setSpecialismsFiltersMap(new Map());
  };

  const schoolsMapClear = () => {
    setSchoolsFiltersMap(new Map());
  };

  const nameFilterClear = () => {
    setNameFilterValue("");
  };

  const yearMapClear = () => {
    setYearFiltersMap(new Map());
  };

  const clearFilters = () => {
    filtersMapClear();
    nameFilterClear();
    schoolsMapClear();
    yearMapClear();
  };

  useEffect(() => {
    // Here use axios to the the app details from the artsthread server.
    // We will get the window variable for the app_id. If development then use a hardcoded one for now.
    // Use .env variable for local and production urls for the artsthread server.
    let app_id = null;
    if (process.env.NODE_ENV !== "production") {
      app_id = "at-app-dmu-2022";
    } else {
      app_id = window.at_app_id;
    }

    axios
      .get(
        process.env.REACT_APP_API_URL +
          "/wp-json/artsthread/v1/apps?app_id=" +
          app_id
      )
      .then((response) => {
        const app_details = response.data;

        // Store the details in state.
        setAppDetails(app_details);
        setAppLoaded(true);

        if (app_details.google_analytics_from_global) {
          // Use the window google analytics rather than React-GAs embed of it.
          ReactGA.initialize(app_details.google_analytics_id, {
            standardImplementation: true,
          });
        } else {
          ReactGA.initialize(app_details.google_analytics_id);
        }
      })
      .catch((error) => {
        setAppLoadingError(true);
      });
  }, []);

  let startThingsUp = <LoadingIcon />;

  if (appLoadingError) {
    startThingsUp = <LoadingError />;
  } else if (appLoaded) {
    const { padding_left, padding_right, padding_bottom, padding_top } =
      appDetails;
    const style = {
      paddingLeft: padding_left + "px",
      paddingRight: padding_right + "px",
      paddingTop: padding_top + "px",
      paddingBottom: padding_bottom + "px",
    };

    let routesClass = null;
    if (appDetails.capitalise_buttons) {
      routesClass = "cap-btns";
    }

    startThingsUp = (
      <div style={style} className={routesClass}>
        <Routes
          app_details={appDetails}
          app_loaded={appLoaded}
          random_seed={randomSeed}
          projects={projects}
          winners={winners}
          projectUpdater={handleProjectsState}
          specialismsFiltersMap={specialismsFiltersMap}
          specialismsFiltersMapUpdate={specialismsFiltersMapUpdate}
          yearFiltersMap={yearFiltersMap}
          yearFiltersMapUpdate={yearFiltersMapUpdate}
          handleNameFilterChange={handleNameFilterChange}
          nameFilterValue={nameFilterValue}
          schoolsFiltersMap={schoolsFiltersMap}
          schoolsFiltersMapUpdate={schoolsFiltersMapUpdate}
          clearFilters={clearFilters}
          updateProjectPageNumber={handleProjectPaginationChange}
          projectsPage={projectsPage}
          totalProjectCount={totalProjectCount}
          projectTotalUpdater={totalProjectsHandler}
        />
      </div>
    );
  }

  return startThingsUp;
};

export default Listings;
