import React, { Component } from "react";
import { withCookies } from "react-cookie";
import axios from "axios";
import styles from "./VoteBox.module.css";
import EmailForm from "./EmailForm/EmailForm";
import qs from "qs";

class VoteBox extends Component {
  state = {
    hasVoted: false,
    emailSubmitted: false,
    previousVote: false,
    voteSent: false,
    userEmail: false,
    showThankyou: false,
  };

  // https://www.npmjs.com/package/react-cookie#user-content-simple-example-with-higher-order-component
  constructor(props) {
    super(props);
    const { cookies } = props;
    const cookie_set = cookies.get("specialism_id_" + this.props.specialism_id);
    const signedup = cookies.get("hasSignedup");
    const stateObj = {};

    if (cookie_set) {
      stateObj["previousVote"] = true;
    }

    if (signedup) {
      stateObj["hasSignedup"] = true;
    }

    this.state = stateObj;
    this.onClick = this.voteHandler.bind(this);
    this.signUpHandler = this.signUpHandler.bind(this);
    this.handleInput = this.handleInput.bind(this);
    this.handleNameInput = this.handleNameInput.bind(this);
    this.handleCheckbox = this.handleCheckbox.bind(this);
  }

  voteHandler() {
    this.setState({ voteSent: true });
    const url =
      process.env.REACT_APP_API_URL + "/wp-json/artsthread/v1/app-vote";
    // Do the axios POST here to update the vote.
    axios
      .post(
        url,
        qs.stringify({
          project_id: this.props.project.id,
          competition_id: this.props.competition_id,
          specialism_id: this.props.specialism_id,
        })
      )
      .then((response) => {
        const result = response.data;
        if (result === "success") {
          // All good store the cookie.
          const { cookies } = this.props;
          // Set the cookie with a name of the specialism id and a value of the project voted for.
          cookies.set(
            "specialism_id_" + this.props.specialism_id,
            this.props.project.id,
            { path: "/" }
          );
          this.setState({ hasVoted: true });
        }
      })
      .catch((error) => {
        alert("There was an error please try again");
      });
  }

  signUpHandler(e) {
    e.preventDefault();
    this.setState({ emailSubmitted: true });

    const url =
      process.env.REACT_APP_API_URL + "/wp-json/artsthread/v1/app-signup";
    axios
      .post(
        url,
        qs.stringify({
          user_email: this.state.userEmail,
          user_name: this.state.userName,
          newsletter: this.state.artsthreadNewsletter,
        })
      )
      .then((response) => {
        const result = response.data;
        if (result === "success") {
          // All good store the cookie.
          const { cookies } = this.props;
          // Set the cookie with a name of the specialism id and a value of the project voted for.
          cookies.set("hasSignedup", true, { path: "/" });
          this.setState({ hasSignedup: true });
          this.setState({ emailSubmitted: false });
          this.setState({ showThankyou: true });
        }
      })
      .catch((error) => {
        alert("There was an error signing you up please try again");
        this.setState({ emailSubmitted: false });
      });
  }

  handleInput(e) {
    const email = e.target.value;
    this.setState({
      userEmail: email,
    });
  }

  handleNameInput(e) {
    const name = e.target.value;
    this.setState({
      userName: name,
    });
  }

  handleCheckbox(e) {
    const el = e.target;
    this.setState({
      artsthreadNewsletter: el.checked,
    });
  }

  render() {
    let button = null;
    if (this.state.previousVote) {
      button = (
        <button
          type="button"
          onClick={() => {
            this.props.goBack();
          }}
          className="at-app-btn at-app-btn--red"
        >
          <i></i>Already voted in this specialism - Continue voting
        </button>
      );
    } else if (!this.state.hasVoted) {
      // If can vote
      let btntext = "Vote for this project";

      if (this.state.voteSent) {
        btntext = "Voting...";
      }

      button = (
        <button
          onClick={this.onClick}
          disabled={this.state.voteSent}
          className="at-app-btn"
        >
          {btntext}
        </button>
      );
    } else {
      // If can't vote show a already voted message in a link element back to the category page. History back button basically.
      button = (
        <button
          className="at-app-btn"
          type="button"
          onClick={() => {
            this.props.goBack();
          }}
        >
          <i></i>Thanks for your vote - Continue voting
        </button>
      );
    }

    let emailform = null;
    if (this.state.hasVoted && !this.state.hasSignedup) {
      emailform = (
        <EmailForm
          handleEmailForm={this.signUpHandler}
          inputChange={this.handleInput}
          inputNameChange={this.handleNameInput}
          checkboxChange={this.handleCheckbox}
          isDisabled={this.state.emailSubmitted}
          hasSignedup={this.state.hasSignedup}
        />
      );
    } else if (this.state.hasSignedup && this.state.showThankyou) {
      emailform = <p>Thank you for signing up!</p>;
    }

    return (
      <div className={styles.votebox}>
        {button}
        {emailform}
      </div>
    );
  }
}

export default withCookies(VoteBox);
