import React from "react";

const CmPartialText = ({ title, text, titleColour }) => {
  const titleStyle = {
    color: titleColour,
  };

  return (
    <>
      {title !== "" ? <h2 style={titleStyle}>{title}</h2> : ""}
      {text !== "" ? <div dangerouslySetInnerHTML={{ __html: text }} /> : ""}
    </>
  );
};

export default CmPartialText;
