import React from "react";
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/opacity.css";

const lazyImage = (props) => {
  return (
    <LazyLoadImage
      alt={props.title}
      src={props.image}
      effect="opacity"
      width={props.width}
      height={props.height}
    />
  );
};

export default lazyImage;
