import CoursesIndex from "../../components/CoursesIndex/CoursesIndex";
import ContentModules from "../../components/Projects/ContentModules/ContentModules";
import React from "react";

const EventCourses = (props) => {
  let header = null;
  if (props.app_details.app_type === "school") {
    header = <ContentModules contentModules={props.app_details.content_modules} />
  }

  return (
    <>
      {header}
      <CoursesIndex
        {...props}
        apiEndpoint="app-school-courses"
        indexType="school"
        queryId={props.app_details.school_id} 
      />
    </>
  );
};

export default EventCourses;
