import React from "react";
import useQueries from "react-use-queries";
import styles from "./GallerySliderWrapper.module.css";
import GallerySlider from "../GallerySlider/GallerySlider";

const GallerySliderWrapper = React.memo(
  (props) => {
    const queries = {
      "(min-width: 375px) and (max-width: 768px)": "medium",
      "(min-width: 768px) and (max-width: 1199px)": "large",
      "(min-width: 1200px)": "xlarge",
    };

    const Wrap = (props) => {
      const [[breakpoint = "small"], mediaQueryListener] = useQueries(queries);
      return (
        <div data-bp={breakpoint} className={styles.GallerySliderWrapper}>
          {mediaQueryListener}
          {props.children}
        </div>
      );
    };

    return (
      <Wrap>
        <GallerySlider
          images={props.images}
          sliderClass={styles.GallerySliderWrapper__slick}
          slideClass={styles.GallerySliderWrapper__slide}
          imageClass={styles.GallerySliderWrapper__image}
          toggleLightbox={props.toggleLightbox}
        ></GallerySlider>
      </Wrap>
    );
  },
  (prevProps, nextProps) => {
    if (prevProps.images === nextProps.images) {
      return true; // props are equal
    }
    return false; // props are not equal -> update the component
  }
);

export default GallerySliderWrapper;
