import React from "react";
import CmFullWidthImage from "./CmFullWidthImage";
import CmFullWidthText from "./CmFullWidthText";
import CmWrapper from "./CmWrapper";
import CmTwoColumnText from "./CmTwoColumnText";
import CmFullWidthVideoCarousel from "./CmFullWidthVideoCarousel";
import CmVideoCarousel from "./CmVideoCarousel";

const ContentModules = ({ contentModules }) => {

  if (!contentModules) {
    return false;
  }

  const modules = contentModules.map((module, i) => {
    let component = null;
    const layout = module.acf_fc_layout;

    if(module.hide_module) return null

    switch (layout) {
      case "full_width_image":
        component = <CmFullWidthImage content={module} />;
        break;
      case "full_width_text":
        component = <CmFullWidthText content={module} />;
        break;
      case "2_column_text":
        component = <CmTwoColumnText content={module} />;
        break;
      case "full_width_video_carousel":
        component = <CmFullWidthVideoCarousel content={module} />;
        break;
      case "portrait_video_carousel":
        // Shuffle the videos in the content before passing to the player

        component = (
          <CmVideoCarousel
            content={module}
            videos={module.videos}
            orientation="portrait"
          />
        );
        break;
      case "landscape_video_carousel":
        // Shuffle the videos in the content before passing to the player

        component = (
          <CmVideoCarousel
            content={module}
            videos={module.videos}
            orientation="landscape"
          />
        );
        break;
      default:
        component = null;
        break;
    }

    return (
      <CmWrapper customCss={module.custom_css} key={i}>
        {component}
      </CmWrapper>
    );
  });

  return <>{modules}</>;
};

export default ContentModules;
