import React from "react";
import ReactModal from "react-modal";
import styles from "./ModalCarousel.module.css";
import SwiperCore, { Navigation } from "swiper";
import { Swiper, SwiperSlide } from "swiper/react";
import ReactPlayer from "react-player";

// install Swiper modules
SwiperCore.use([Navigation]);

ReactModal.setAppElement("#root");

const ModalCarousel = ({
  modalOpen,
  activeSlide,
  closeModal,
  videos,
  handleActiveSlide,
  slideChangeHandler,
  orientation,
}) => {
  let modalClass;

  if (orientation === "portrait") {
    modalClass = styles.modalcarousel__player_portrait;
  } else {
    modalClass = styles.modalcarousel__player;
  }
  return (
    <>
      <ReactModal
        isOpen={modalOpen}
        className={styles.modalcarousel__content}
        overlayClassName={styles.modalcarousel__overlay}
        onRequestClose={closeModal}
      >
        <Swiper
          navigation
          initialSlide={activeSlide}
          onSlideChangeTransitionEnd={slideChangeHandler}
        >
          {videos.map((video, i) => {
            return (
              <SwiperSlide
                key={i}
                className={styles.videocarouselswiper__slide}
              >
                <div className={styles.videocarouselswiper__slide__inner}>
                <div className={modalClass}>
                  {activeSlide === i && (
                  <ReactPlayer
                    url={video.vimeo_url}
                    controls={true}
                    playing={activeSlide === i}
                    onPlay={() => handleActiveSlide(i)}
                  />
                  )}
                </div>
                </div>
              </SwiperSlide>
            );
          })}
        </Swiper>
        <button onClick={closeModal} className={styles.modalcarousel__close}>
          Close
        </button>

        <div className={styles.modalcarousel__slidecount}>
          {activeSlide + 1} of {videos.length}
        </div>
      </ReactModal>
    </>
  );
};

export default ModalCarousel;
