import React from "react";
import styles from "./ProjectsWrapper.module.css";
import useQueries from "react-use-queries";
import ProjectPreview from "../ProjectPreview/ProjectPreview";
import LoadingIcon from "../../LoadingIcon/LoadingIcon";
import LoadingError from "../../LoadingError/LoadingError";
import LoadingNoResults from "../../LoadingNoResults/LoadingNoResults";
import { queries } from "../../config";

const ProjectsWrapper = React.memo(
  (props) => {
    let content = null;

    const ProjectsProjects = (props) => {
      const [[breakpoint = "small"], mediaQueryListener] = useQueries(queries);
      return (
        <div data-bp={breakpoint} className={styles.Projects__projects}>
          {mediaQueryListener}
          {props.children}
        </div>
      );
    };

    /* If there is an error show an error.
    If not and projects are still loading show a loading spinner
    Else show the projects content */
    if (props.loadingError) {
      content = <LoadingError />;
    } else if (!props.projects) {
      content = <LoadingIcon />;
    } else if (props.projects.length === 0) {
      content = <LoadingNoResults />;
    } else if (props.projects.length > 0) {
      content = props.projects.map((project) => {
        return (
          <ProjectPreview
            key={project.id}
            title={project.title}
            firstname={project.firstname}
            surname={project.surname}
            image={project.image}
            slug={project.slug}
            specialisms={project.specialisms}
            userLocation={project.location}
            winner={props.winner}
          />
        );
      });
    }

    return <ProjectsProjects>{content}</ProjectsProjects>;
  },
  (prevProps, nextProps) => {
    if (prevProps.projects === nextProps.projects) {
      return true; // props are equal
    }
    return false; // props are not equal -> update the component
  }
);

export default ProjectsWrapper;
