import styles from "./CourseLinkWrapper.module.css";
import CourseLink from "../../components/CourseLink/CoursesLink";
import useQueries from "react-use-queries";
import React from "react";
import { queries } from "../../components/config";

const courseLinkWrapper = (props) => {
  const courseLinks = props.courses.map((course) => {
    return (
      <CourseLink
        key={course.id}
        id={course.id}
        name={course.name}
        school={course.school}
        image={course.image}
      />
    );
  });

  const CourseCourseLinks = (props) => {
    const [[breakpoint = "small"], mediaQueryListener] = useQueries(queries);
    return (
      <div data-bp={breakpoint} className={styles.CourseLinkWrapper}>
        {mediaQueryListener}
        {props.children}
      </div>
    );
  };

  return <CourseCourseLinks>{courseLinks}</CourseCourseLinks>;
};

export default courseLinkWrapper;
