import React, {useState} from 'react';
import "slick-carousel/slick/slick.css";
import Slider from "react-slick";
import Preload from 'react-preload';

const GallerySlider = (props)=> {

  const [imagesLoaded, setImagesLoaded] = useState(0);

  const slickSettings = {
    variableWidth: true,
    arrows: true,
    infinite: true,
    draggable: false,
    // lazyLoad: 'progressive',
  };

  /* Create an array of images to preload */
  const images = props.images.map((image, index) => {
    return image.image_medium;
  });

  /* Create the slides that will be placed inside the slick carousel */
  const slides = props.images.map((image, index) => {
    return (
      <div key={image.image_medium} className={props.slideClass}>
        <img src={image.image_medium} alt="" className={props.imageClass} onClick={() => props.toggleLightbox(index)} />
      </div>
    );
  });

  const initSlickWithDelay = () => {
    setTimeout(() => {
      setImagesLoaded({imagesLoaded: true})
    }, 200);
  }

  let slickClass = null;
  if(imagesLoaded) {
      slickClass = 'slick-fadein';
  }

  return(
    <Preload
      images={images}
      onSuccess={initSlickWithDelay}
    >
      <Slider {...slickSettings} className={props.sliderClass + ' ' + slickClass}>
        {slides}
      </Slider>
    </Preload>
  );

}

export default GallerySlider;